import React, { useState } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { ChevronDown } from 'lucide-react'
import { parseDesignHighlights } from '../../utils/productUtils'

interface DesignHighlightsProps {
  description: string
}

const DesignHighlights: React.FC<DesignHighlightsProps> = ({ description }) => {
  const [expandedHighlight, setExpandedHighlight] = useState<number | null>(null)
  const highlights = parseDesignHighlights(description)

  if (!description || highlights.length === 0) return null

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ delay: 0.25 }}
      className="space-y-2"
    >
      <h3 className="text-lg font-bold text-gray-800 mb-3 flex items-center gap-2">
         Design Highlights
      </h3>
      {highlights.map((highlight, idx) => (
        <motion.div
          key={idx}
          initial={{ opacity: 0, x: -20 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ delay: 0.3 + idx * 0.1 }}
        >
          <motion.button
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
            onClick={() => setExpandedHighlight(expandedHighlight === idx ? null : idx)}
            className="w-full bg-gradient-to-r from-orange-500 to-orange-600 hover:from-orange-600 hover:to-orange-700 text-white rounded-xl p-4 text-left transition-all shadow-md hover:shadow-lg"
          >
            <div className="flex items-center justify-between">
              <span className="font-semibold">{highlight.title}</span>
              <motion.div
                animate={{ rotate: expandedHighlight === idx ? 180 : 0 }}
                transition={{ duration: 0.2 }}
              >
                <ChevronDown className="w-5 h-5" />
              </motion.div>
            </div>
            <AnimatePresence>
              {expandedHighlight === idx && (
                <motion.div
                  initial={{ height: 0, opacity: 0 }}
                  animate={{ height: 'auto', opacity: 1 }}
                  exit={{ height: 0, opacity: 0 }}
                  transition={{ duration: 0.3 }}
                  className="overflow-hidden"
                >
                  <p className="mt-3 text-orange-50 text-sm leading-relaxed">
                    {highlight.description}
                  </p>
                </motion.div>
              )}
            </AnimatePresence>
          </motion.button>
        </motion.div>
      ))}
    </motion.div>
  )
}

export default DesignHighlights
